.custom-btn {
    width: 100%;
}

.custom-btn2{
    padding-bottom: 150px;
}

.custom-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
}
.custom-alert {
    left: 0;
    margin: auto; 
    position: fixed;
    right: 0;
    text-align: center;
    top: 3em;
    width: 30%;
    z-index: 1;
}

.user-form {
    display: flex;
    justify-content: right;
    padding-bottom: 10px;
}


.user-list{
    display: flex;
    justify-content: right;
}

.login-form {
    display: flex;
    justify-content: center;
}

.people-list{
    margin-left: 30px;
    margin-right: 30px;
    padding-top: 5%;
}
h3{
    padding: 5px;
}
.custom-a{
    font-size: large;
    text-decoration: none;
    color: inherit;
}
a::before{
    content: '';
}
.rules-ul{
    list-style-type: none;
}
.rules{
    display: flex;
    flex-direction: row;
    padding-right: 13%;
    flex: 1;
}
.rules-text{
    padding-left: 250px;
}
.rules-list{
    padding: 12px;
}
.inner-text{
    font-size: 17px;
    padding-left: 30px;
}
.navigation{
    padding-left: 24%;
    position: -webkit-sticky;
    position: sticky;
}
.custom-ul{
    font-size: 17px;
}
.sidebar{
    position: fixed;
    padding-top: 120px;
    transition: 1s;
}
.center-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
}
.home{
    padding-top: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-direction: column;
}

.home-text{
    font-size: x-large;
    text-align: center;
}

@media only screen and (max-width : 800px) { /*Puhelimet ja pienet laitteet*/
    .navigation{
        display: none;
    }
    .rules{
        display: flex;
        justify-content: center;
        align-items: center;
        padding-right: 0%;
        flex: 1;
    }
    .rules-text{
        padding-left: 5px;
    }
    .inner-text{
        font-size: 15px;
        padding-left: 10px;
        padding-right: 10px;
    }
    .custom-ul{
        font-size: 15px;
        padding-right: 10px;
    }
    .nav{
        display: none;
    }
    .mobileNav{
        width: 100%;
        font-size: large;
    }
    .form{
        padding-top: 20px;
        padding-left: 3%;
        padding-right: 3%;
    }
    .rulesPage{
        padding-top: 20px;
    }
    .home{
        padding-top: 20px;
    }
    .navLogo{
        display: none;
    }
    .login-form {
        display: flex;
        justify-content: center;
        padding-top: 20px;
    }
    .navLinks{  
        display: flex;
        justify-content: center;
        align-items: center;
        padding-right: 0%;
        flex: 1;
    }
    .center-logo{
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 15px;
        padding-bottom: 15px;
    }
    .rulesPage{
        padding-top: 20px;
    }
}
@media only screen and (min-width : 801px) and (max-width : 1300px) { /*tabletit ja isommat laitteet*/
    .navigation{
        padding-left: 5%;
        position: -webkit-sticky;
        position: sticky;
    }
    .rules-ul{
        list-style-type: none;
    }
    .rules{
        display: flex;
        flex-direction: row;
        padding-right: 4%;
        flex: 1;
    }
    .rules-text{
        padding-left: 250px;
    }
    .rules-list{
        padding: 8px;
    }
    .inner-text{
        font-size: 17px;
        padding-left: 30px;
    }
    .custom-ul{
        font-size: 17px;
    }
    .sidebar{
        position: fixed;
        padding-bottom: 22px;
        transition: 1.5s;
    }
    .nav{
        display: none;
    }
    .mobileNav{
        width: 100%;
        font-size: large;
    }
    .form{
        padding-top: 20px;
    }
    .rulesPage{
        padding-top: 20px;
    }
    .home{
        padding-top: 20px;
    }
    .login-form {
        display: flex;
        justify-content: center;
        padding-top: 20px;
    }
}